import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useCognito } from '../context/CognitoProvider';

function Login() {
  const { login } = useCognito();
  const [name, setName] = useState('');
  const [password, setPassword] = useState('');
  const [errorMessage, setErrorMessage] = useState('');

  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await login(name, password);
      console.log(' ');
      navigate("/");
    } catch (error) {
      console.error(' :', error);
      if (error.code === "UserNotConfirmedException") {
        setErrorMessage("        .");
      } else {
        setErrorMessage("   .");
      }
    }
  };

  return (
    <div className="min-h-screen flex justify-center items-center bg-gray-100">
      <div className="max-w-md w-full bg-white p-8 rounded-lg shadow">
        <h2 className="text-2xl font-bold mb-4"></h2>
        <form onSubmit={handleSubmit}>
          <div className="mb-4">
            <label htmlFor="name" className="block text-sm font-medium text-gray-700"></label>
            <input type="text" id="name" placeholder="" value={name} onChange={(e) => setName(e.target.value)} className="mt-1 w-full p-2 rounded border border-gray-300" required />
          </div>
          <div className="mb-4">
            <label htmlFor="password" className="block text-sm font-medium text-gray-700"></label>
            <input type="password" id="password" placeholder="" value={password} onChange={(e) => setPassword(e.target.value)} className="mt-1 w-full p-2 rounded border border-gray-300" required />
          </div>
          <button type="submit" className="w-full bg-blue-500 text-white py-2 px-4 rounded hover:bg-blue-700 focus:outline-none focus:shadow-outline"></button>
          {errorMessage && <p className="text-red-500 text-sm mt-4">{errorMessage}</p>}
        </form>
        <p className="mt-4 text-center text-sm">
           ? <Link to="/signup" className="text-blue-500 hover:text-blue-700"></Link>
        </p>
      </div>
    </div>
  );
}

export default Login;
